import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import axios from "axios";
axios.defaults.baseURL = "http://localhost:9999/api/v1";

Vue.prototype.$axios = axios;

import "./mock";

import "amfe-flexible";

import {
  Button,
  Cell,
  CellGroup,
  NavBar,
  Search,
  Grid,
  GridItem,
  Card,
  Tab,
  Tabs
} from "vant";
Vue.use(Button)
  .use(Cell)
  .use(CellGroup)
  .use(NavBar)
  .use(Search)
  .use(Grid)
  .use(GridItem)
  .use(Card)
  .use(Tab)
  .use(Tabs);

import "./style/bass.css";

Vue.config.productionTip = false;

new Vue({
  router,
  render: h => h(App)
}).$mount("#app");
