<template>
  <div class="index">
    <van-search
      v-model="value"
      @change="queryChange()"
      placeholder="回车搜索项目名称"
    />
    <van-tabs v-model="active">
      <van-tab title="备案项目列表">
        <!-- 备案项目列表 -->
        <div class="top">
          <van-grid :column-num="6">
            <van-grid-item text="备案编号" />
            <van-grid-item text="项目名称" />
            <van-grid-item text="状态" />
            <van-grid-item text="进度" />
            <van-grid-item text="备案通过日期" />
            <van-grid-item text="操作" />
          </van-grid>
          <van-grid
            :column-num="6"
            v-for="(item, index) in recProjectList"
            :key="index"
          >
            <van-grid-item :text="item.number" />
            <van-grid-item :text="item.name" />
            <van-grid-item :text="item.status" />
            <van-grid-item :text="item.schedule" />
            <van-grid-item :text="item.dateDay" />
            <van-button
              type="primary"
              size="small"
              @click="getDicBaById(item.id)"
              >详情</van-button
            >
          </van-grid>
        </div>
      </van-tab>
      <van-tab title="核准项目列表">
        <!-- 核准项目列表  -->
        <div class="top">
          <van-grid :column-num="5">
            <van-grid-item text="项目代码" />
            <van-grid-item text="项目名称" />
            <van-grid-item text="管理部门" />
            <van-grid-item text="核准日期" />
            <van-grid-item text="操作" />
          </van-grid>
          <van-grid
            :column-num="5"
            v-for="(item, index) in chkProjectList"
            :key="index"
          >
            <van-grid-item :text="item.number" />
            <van-grid-item :text="item.name" />
            <van-grid-item :text="item.schedule" />
            <van-grid-item :text="item.date" />
            <van-button type="primary" size="small" @click="getDicById(item.id)"
              >详情</van-button
            >
          </van-grid>
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: 0,
      value: "",
      recProjectList: [], // 备案项目列表
      chkProjectList: []
    };
  },
  created() {
    this.createList();
    this.createListChk();
  },
  methods: {
    createList() {
      // 获取备案项目列表
      this.$axios.get("/recProjectList").then(res => {
        this.recProjectList = res.data.data;
      });
    },
    createListChk() {
      // 获取核准项目列表
      this.$axios.get("/chkProjectList").then(res => {
        this.chkProjectList = res.data.data;
      });
    },
    queryChange() {
      if (this.active == 0) {
        var list = this.recProjectList.filter(item =>
          item.name.includes(this.value)
        );
        this.recProjectList = list;
      } else {
        var list = this.chkProjectList.filter(item =>
          item.name.includes(this.value)
        );
        this.chkProjectList = list;
      }
    },
    // 根据id获取备案项目列表详情
    async getDicBaById(id) {
      this.$router.push({
        path: "/recProjectList",
        query: {
          id: id
        }
      });
    },
    // 根据id获取核准项目列表详情
    async getDicById(id) {
      this.$router.push({
        path: "/chkProjectList",
        query: {
          id: id
        }
      });
    }
  },
  watch: {
    value: function(newValue, oldBalue) {
      newValue.replace(/(^\s*)|(\s*$)/g, "");
      if (newValue == "") {
        this.createList();
        this.createListChk();
      }
    }
  }
};
</script>

<style lang="less" scoped>
.top {
  margin-top: 50px;
}
</style>
